.login-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
}

.login-form{
  width: 80%;
  max-width: 400px;
}

.login-email-input{
  margin: 10px;
  /* width: 400px; */
}

.login-password-input{
  margin: 10px;
  /* width: 400px; */
}

.login-button{
  margin: 10px;
  /* width: 400px; */
}

/* -------------------PROFILE CSS----------------------------- */

.profile-wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
  text-align: center;
}

.profile-name-entry{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  
}
.native-language-entry{
max-width: 400px;
margin: 10px;
}

.profile-language-entry{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.profile-entry{
  margin: 10px;
}

.profile-pic-section{
  display: flex;
  flex-direction: row;
}

.profile-pic-display-wrapper{
width: 100px;
height: 100px;
margin: 10px;
margin-right: 30px;
margin-bottom: 20px;

}

.profile-pic-display{
  position: absolute;
  max-width: 100px;
  border-radius: 50%;
  border-style: solid;
  border-width: 1px;
  border-color: gray;
}
.profile-pic-display-button{
position:absolute;
top: 35px;
left: 20px;
}

#profile-pic-upload{
  display: none;
}

.profile-pic-cropper{
  position: relative;
  height: 300px;
  width: 300px;
  margin: 10px;
}

.cropper-wrapper{
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 10%;
  background-color: white;
  width: 400px;
  height: 400px;
  border: solid;
  border-color: grey;
  border-width: 1px;
  z-index: 1000;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 10px;
}

.cropper-close-icon{
position: relative;
top: 5px;
left: 95px;
}

/* -------------------CREATE GROUP CSS----------------------------- */

.create-group-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
  text-align: center;
}

.create-group-form{
  width: 80%;
  max-width: 400px;
}

.create-group-form-entry{
  margin: 10px;
}


/* ------------------- GROUP PAGE CSS----------------------------- */
.group-page-wrapper{
display: flex;
flex-direction: row;
flex-wrap: wrap;
border: solid;
border-width: 1px;

}

.group-page-sidebar{
  border: solid;
  border-width: 1px;
  margin: 10px;
  padding: 5px;
}

.group-page-sidebar-element{
  border: solid;
  border-width: 1px;
  width: 250px;
  margin: 10px;
  padding: 5px;
}

.group-page-main{
  flex: auto;
  border: solid;
  border-width: 1px;
  margin: 10px;
  padding: 5px;
  
}



/* ------------------- CHATS PAGE CSS----------------------------- */

.main-chats-wrapper{
margin-top: 50px;
display: flex;
flex-direction: column;
height: 85vh;
}

.main-chats-body-wrapper{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  height: 100%;
}

.contacts-list{
  width: 300px;
  height: 100%;
  margin-right: 3px;
}

.chat-log-wrapper{
  flex: 1;
  height:100%;
  
}

.chats-wrapper{
  background-color: rgb(244, 252, 253);
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 5px;
  overflow-y: scroll;
  height: 90%;
}

.chat-header{
height: 10%;
}

.chat-bottom-section{
  position: sticky;
  bottom: 0;
  display: flex;
  flex-direction: column;
}

.chat-typing-notice{
  font-size: 12px;
  margin-bottom: 3px;
  margin-left: 15px;
}

.chat-input-wrapper{
  display: flex;
  flex-direction: row;
  background-color: rgb(255, 255, 255);
  padding: 5px;
  align-items: center;
  border-radius: 30px;
}

.chat-input{
flex: 1;
height: 30px;
border-width: 0;
border-color: gray;
border-radius: 30px;
padding: 20px;
margin: 2px;
background-color: antiquewhite;
}

.chat-button{
margin: 5px;
}

.chat-block{
  display: flex;
  flex-direction: row;
}

.chat-avatar{
margin-top: 5px;
}

.chat-sender-name{
margin: 0;
font-size: 12px;
 }

 .chat-message-wrapper{
  background-color: white;
  flex-shrink: 2;
  overflow-y: auto;
  width: fit-content;
  margin: 5px;
  border-radius: 15px;
  padding: 7px;
  box-shadow: 1px 1px #787878;
}

.chat-message{
  text-align: center;
  margin: 5px;
}

.chat-time-wrapper{
  position: relative;
  width: 60px;
  opacity: 0;
}

.chat-time-wrapper:hover{
  position: relative;
  width: 60px;
  opacity: 100;
}

.chat-time{
  font-size: 9px;
  position: absolute;
  bottom: 0;
}


.chat-info-drawer{
min-height: 200px;
}



